body, html{
  background-color: #414345;
}
button{
  width: 5rem;
  height: 5rem;
  margin: 0.2rem;
  color: #fff;
  border-radius: 5%;
  border: none;
  font-size: 1.5rem;
  background: #232526;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #414345, #232526);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.btn-orange{
  background: #FF512F;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #F09819, #ff862a);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #F09819, #ff862a); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.calculator-body{
  background: #fff;
  width: fit-content;
  padding: 1rem;
  margin: 0 auto;
  margin-top: 2%;
  border-radius: 2%;
}

.result{
  text-align: right;
  font-size: 2rem;
  height: 4rem;
  margin: 0.2rem;
  color: #fff;
  border-radius: 2%;
  border: none;
  background: #232526;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #414345, #232526);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}